.nav-bar {
    background: #f3aca6;
    width: 64px;
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 3;
    min-height: 500px;

    .logo{
        display: block;
        padding: 8px 0;

        img{
            display: block;
            margin: 8px auto;
            width: 52px;
            height: auto;
        }
    }
    nav {
        display: block;
        text-align: center;
        position: absolute;
        height: 210px;
        top: 50%;
        margin-top: -120px;
        width: 100%;

        a {
            font-size: 22px;
            color: #b7676b;
            display: block;
            line-height: 51px;
            height: 51px;
            position: relative;
            text-decoration: none;

            i {
                transition: all 0.3s ease-out;
            }

            &:hover {
                color: #b7676b;

                svg {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
                }
            }

            &:after {
                content: '';
                font-size: 9px;
                letter-spacing: 2px;
                position: absolute;
                bottom: 0;
                display: block;
                width: 100%;
                text-align: center;
                opacity: 0;
                transition: all 0.3s ease-out;
            }

            &:first-child {
                &::after {
                    content: 'HOME';
                    font-weight: 900;
                    letter-spacing: 1px;
                }
            }
        }

        a.about-link {
            &:after{
                content: 'ABOUT';
                font-weight: 900;
                letter-spacing: 1px;
            }
        }

        a.contact-link {
            &:after{
                content: 'CONTACT';
                font-weight: 900;
                letter-spacing: 1px;
            }
        }

        a.projects-link {
            &:after{
                content: 'PROJECTS';
                font-weight: 900;
                letter-spacing: 1px;
            }
        }

        a.active {
            svg {
                color: #b7676b;
            }
        }
    }
}

ul {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: block;
    padding: 0;
    list-style: none;
    text-align: center;
    margin: 0;

    li {
        a{
            padding: 7px 0;
            display: block;
            font-size: 15px;
            line-height: 16px;

            &:hover svg {
                color: #b7676b;
            }
        }
    }
}